'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';

interface Doctor {
  _id: string;
  full_name: string;
  specializations: string[];
  clinic_affiliations?: any[];
}

export default function DoctorsPage() {
  const [doctors, setDoctors] = useState<Doctor[]>([]);
  const [filteredDoctors, setFilteredDoctors] = useState<Doctor[]>([]);
  const [specialization, setSpecialization] = useState('');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDoctors();
  }, []);

  const fetchDoctors = async () => {
    try {
      const response = await fetch('/api/doctors');
      const data = await response.json();
      setDoctors(data);
      setFilteredDoctors(data);
    } catch (error) {
      console.error('Error fetching doctors:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleFilter = async (spec: string) => {
    setSpecialization(spec);

    if (!spec) {
      setFilteredDoctors(doctors);
      return;
    }

    try {
      const response = await fetch(`/api/doctors/search?specialization=${spec}`);
      const data = await response.json();
      setFilteredDoctors(data);
    } catch (error) {
      console.error('Error searching doctors:', error);
    }
  };

  if (loading) return <div className="text-center py-12">Loading doctors...</div>;

  return (
    <div className="max-w-7xl mx-auto px-4 py-12">
      <h1 className="text-4xl font-bold mb-8">Our Doctors</h1>

      {/* Filter */}
      <div className="mb-8 bg-white rounded-lg shadow p-6">
        <label className="block text-gray-700 font-bold mb-2">
          Filter by Specialization
        </label>
        <input
          type="text"
          placeholder="e.g., Cardiology, Surgery..."
          value={specialization}
          onChange={(e) => handleFilter(e.target.value)}
          className="w-full px-4 py-2 border border-gray-300 rounded"
        />
      </div>

      {filteredDoctors.length === 0 ? (
        <div className="bg-white rounded-lg shadow p-6 text-center">
          <p className="text-gray-600">No doctors found.</p>
        </div>
      ) : (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredDoctors.map((doctor) => (
            <Link key={doctor._id} href={`/doctors/${doctor._id}`}>
              <div className="bg-white rounded-lg shadow p-6 hover:shadow-lg cursor-pointer">
                <h2 className="text-xl font-bold mb-2">{doctor.full_name}</h2>
                <p className="text-gray-600 mb-4">
                  {doctor.specializations.join(', ') || 'General Practice'}
                </p>
                <button className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700">
                  View Profile
                </button>
              </div>
            </Link>
          ))}
        </div>
      )}
    </div>
  );
}
